define('glympse-adapter/adapter/models/MemberTicket',['require','exports','module','glympse-adapter/lib/utils'],function(require, exports, module)
{
    'use strict';

	var lib = require('glympse-adapter/lib/utils');


	// Exported class
	function MemberTicket(data, cfg)
	{
		// state

		// consts
		var dbg = lib.dbg('MemberTicket', cfg.dbg);

		// TODO: Just map data props directly??
		//	---> Only want immediate non-Objects/Arrays
		var props = [ 'invite_code'
					, 'ticket_id'
					, 'created_time'
					];



		///////////////////////////////////////////////////////////////////////////////
		// PROPERTIES
		///////////////////////////////////////////////////////////////////////////////

		// NOTE: some properties created via lib.mapProps

		this.getData = function()
		{
			return data;
		};

		this.setData = function(val)
		{
			data = val;
			lib.mapProps(this, props, data);
		};


		///////////////////////////////////////////////////////////////////////////////
		// PUBLICS
		///////////////////////////////////////////////////////////////////////////////

		this.toJSON = function()
		{
			return data;
		};


		///////////////////////////////////////////////////////////////////////////////
		// UTILITY
		///////////////////////////////////////////////////////////////////////////////


		///////////////////////////////////////////////////////////////////////////////
		// CTOR
		///////////////////////////////////////////////////////////////////////////////

		this.setData(data);
	}

	// MemberTicket defines


	module.exports = MemberTicket;
});

